"use client";

import Header from "@/components/Header";
import Footer from "@/components/Footer";
import { ANALYTICS_ENABLED } from "@/utils/analyticsConfig";
import { disableHotjarInDevelopment } from "@/utils/analyticsConfig";
import { disableSimpleAnalyticsInDevelopment } from "@/utils/analyticsConfig";
import { useEffect } from "react";
import "@/styles/globals.css";

export default function RootLayout({ children }: { children: React.ReactNode }) {
  useEffect(() => {
    if (ANALYTICS_ENABLED) {
      disableHotjarInDevelopment();
      disableSimpleAnalyticsInDevelopment();
    }
  }, []);

  return (
    <html lang="en">
      <body>
        <Header />
        <main>{children}</main>
        <Footer />
      </body>
    </html>
  );
}
